.card {
  display: flex;
  justify-content: center;
  flex-grow: 1;
}

.card main {
  width: 100%;
  max-width: 700px;
  max-height: 40vm;
  background: #fff;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  padding: 20px;
}

/* Form Styles */
form {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

form input {
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 1rem;
  width: 100%;
}

form button {
  padding: 10px;
  background: #7b7f03;
  color: #060505;
  border: none;
  width: 20%;
  margin-left: 40%;
  border-radius: 4px;
  font-size: 1.5rem;
  font-weight: bold;
  cursor: pointer;
}

form button:hover {
  background: #d1d161;
}`, "",{"version":3,"sources":["webpack://./src/client/styles/form.scss"],"names":[],"mappings":"AAAA,cAAA;AACA;EACE,aAAA;EACA,uBAAA;EAEA,YAAA;AAAF;;AAGA;EACE,WAAA;EACA,gBAAA;EACA,gBAAA;EACA,gBAAA;EACA,kBAAA;EACA,wCAAA;EACA,aAAA;AAAF;;AAIA,gBAAA;AACA;EACE,aAAA;EACA,sBAAA;EACA,SAAA;AADF;;AAIA;EACE,aAAA;EACA,sBAAA;EACA,kBAAA;EACA,eAAA;EACA,WAAA;AADF;;AAIA;EACE,aAAA;EACA,mBAAA;EACA,cAAA;EACA,YAAA;EACA,UAAA;EACA,gBAAA;EACA,kBAAA;EACA,iBAAA;EACA,iBAAA;EACA,eAAA;AADF;;AAIA;EACE,mBAAA;AADF","sourcesContent":["/* Form Card */\n.card {\n  display: flex;\n  justify-content: center;\n  // align-items: center;\n  flex-grow: 1;\n}\n\n.card main {\n  width: 100%;\n  max-width: 700px;\n  max-height: 40vm;\n  background: #fff;\n  border-radius: 8px;\n  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);\n  padding: 20px;\n  // text-align: center;\n}\n\n/* Form Styles */\nform {\n  display: flex;\n  flex-direction: column;\n  gap: 10px;\n}\n\nform input {\n  padding: 10px;\n  border: 1px solid #ccc;\n  border-radius: 4px;\n  font-size: 1rem;\n  width: 100%;\n}\n\nform button {\n  padding: 10px;\n  background: #7b7f03;\n  color: #060505;\n  border: none;\n  width: 20%;\n  margin-left: 40%;\n  border-radius: 4px;\n  font-size: 1.5rem;\n  font-weight: bold;\n  cursor: pointer;\n}\n\nform button:hover {\n  background: #d1d161;\n}\n"],"sourceRoot":""}]);
