*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* Remove built-in styles for common elements */
body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
figure,
ol,
ul {
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
  color: inherit;
}

img,
picture,
video,
canvas,
svg {
  display: block;
  max-width: 100%;
}

button,
input,
select,
textarea {
  font: inherit;
  margin: 0;
  padding: 0;
  border: none;
  background: none;
  color: inherit;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

ul,
ol {
  list-style: none;
}

html {
  scroll-behavior: smooth;
  -webkit-text-size-adjust: 100%; /* Prevent font scaling in landscape mode */
}

/* Default body styles */
body {
  font-family: Arial, sans-serif;
  line-height: 1.6;
  color: #333;
  background-color: #f4f4f9;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

/* Remove focus outline for accessibility (reapply with visible styles when focused via keyboard) */
:focus {
  outline: none;
}

:focus-visible {
  outline: 2px solid #0078d4;
  outline-offset: 2px;
}`, "",{"version":3,"sources":["webpack://./src/client/styles/resets.scss"],"names":[],"mappings":"AAAA,uBAAA;AACA;;;EAGE,SAAA;EACA,UAAA;EACA,sBAAA;AACF;;AAEA,+CAAA;AACA;;;;;;;;;;;;EAYE,SAAA;EACA,UAAA;AACF;;AAEA;EACE,qBAAA;EACA,cAAA;AACF;;AAEA;;;;;EAKE,cAAA;EACA,eAAA;AACF;;AAEA;;;;EAIE,aAAA;EACA,SAAA;EACA,UAAA;EACA,YAAA;EACA,gBAAA;EACA,cAAA;AACF;;AAEA;EACE,yBAAA;EACA,iBAAA;AACF;;AAEA;;EAEE,gBAAA;AACF;;AAEA;EACE,uBAAA;EACA,8BAAA,EAAA,2CAAA;AACF;;AAEA,wBAAA;AACA;EACE,8BAAA;EACA,gBAAA;EACA,WAAA;EACA,yBAAA;EACA,iBAAA;EACA,aAAA;EACA,sBAAA;AACF;;AAEA,mGAAA;AACA;EACE,aAAA;AACF;;AAEA;EACE,0BAAA;EACA,mBAAA;AACF","sourcesContent":["/* Extended CSS Reset */\n*,\n*::before,\n*::after {\n  margin: 0;\n  padding: 0;\n  box-sizing: border-box;\n}\n\n/* Remove built-in styles for common elements */\nbody,\nh1,\nh2,\nh3,\nh4,\nh5,\nh6,\np,\nblockquote,\nfigure,\nol,\nul {\n  margin: 0;\n  padding: 0;\n}\n\na {\n  text-decoration: none;\n  color: inherit;\n}\n\nimg,\npicture,\nvideo,\ncanvas,\nsvg {\n  display: block;\n  max-width: 100%;\n}\n\nbutton,\ninput,\nselect,\ntextarea {\n  font: inherit;\n  margin: 0;\n  padding: 0;\n  border: none;\n  background: none;\n  color: inherit;\n}\n\ntable {\n  border-collapse: collapse;\n  border-spacing: 0;\n}\n\nul,\nol {\n  list-style: none;\n}\n\nhtml {\n  scroll-behavior: smooth;\n  -webkit-text-size-adjust: 100%; /* Prevent font scaling in landscape mode */\n}\n\n/* Default body styles */\nbody {\n  font-family: Arial, sans-serif;\n  line-height: 1.6;\n  color: #333;\n  background-color: #f4f4f9;\n  min-height: 100vh;\n  display: flex;\n  flex-direction: column;\n}\n\n/* Remove focus outline for accessibility (reapply with visible styles when focused via keyboard) */\n:focus {\n  outline: none;\n}\n\n:focus-visible {\n  outline: 2px solid #0078d4;\n  outline-offset: 2px;\n}\n"],"sourceRoot":""}]);
